html, body {
    box-sizing: border-box;
    overflow-x: hidden;
}

ul li{
    list-style: none;
}

ul{
    padding-block-start: 0;
    margin-block-start: 0;
    padding: 0;
    margin: 0;
}

*{
    font-family: "Source Sans 3", sans-serif;
}

a{
    text-decoration: none;
}

a:hover{
    text-decoration: none!important;
    transition: .3s!important;
    color: #fff!important;
    opacity: .8;
}

.global-container{
    width: min(1170px, 100%);
    margin: 0 auto;
    padding: 0 20px;
    box-sizing: border-box;
}